async function fetchPokemon(name, url) {
  const res = await fetch(`${url}/${name}`);
  //   -   404,     catch.  404        :
  if (res.ok) {
    return res.json(); //   ,     then (,  )
  }

  //      catch   Promise.reject:
  return await Promise.reject(
    new Error(`  ' ${name}  `),
  );
}

export { fetchPokemon };
